















import Swal from 'sweetalert2';
import { Component, Ref, Vue } from 'vue-property-decorator';
import { Action } from 'vuex-class';
import { IInputInfo, ILoginPayload } from '@/types/interface';
import { getLostPhone } from '@/utility';
import * as commonTypes from '../../types/common';
import banner from '../../components/Banner.vue';
import inputFormPanel from '../../components/InputFormPanel.vue';

@Component({
  components: {
    banner,
    inputFormPanel,
  },
})

export default class SetNewPassword extends Vue {
  @Action('resetLoginInfoCode') resetLoginInfoCode!: () => void;
  @Ref('inputForm') readonly inputForm!: inputFormPanel;

  isExpire = false;
  errorTxt = '';
  timer = 0;

  created() {
    this.timer = setTimeout(() => {
      this.isExpire = true;
      this.errorTxt = '超過時效，請返回上一步重新驗證';
      this.resetLoginInfoCode();
    }, 600000);
  }

  beforeDestroy() {
    clearTimeout(this.timer);
  }

  linkBack() {
    this.$router.push('/login/forget_password');
  }

  send() {
    if (this.isExpire) return;
    const info: IInputInfo | undefined = this.inputForm.getNewPassword();
    if (info) {
      this.isExpire = true;
      getLostPhone(info)
        .then((data: ILoginPayload) => {
          if (data.Code === '0000') {
            Swal.fire({
              width: 288,
              showCloseButton: false,
              background: '#FFFFFF',
              title: '密碼設定成功',
              text: '請使用新密碼登入',
              customClass: {
                title: 'modal-title',
                content: 'modal-content',
                confirmButton: 'modal-confirm-button',
              },
              confirmButtonText: '返回登入',
            }).then(() => {
              this.resetLoginInfoCode();
              this.$router.push('/');
            });
          } else {
            this.errorTxt = data.Message || '';
            this.resetLoginInfoCode();
          }
        });
    }
  }

  inputList = [commonTypes.INPUT_NEW_PASSWORD];
}
